/* Deals with padding and margin box overflow */
*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  font-family: Fira Sans Regular, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  color: #333336;
  background: #333333; /* fallback for old browsers */
  background: -webkit-linear-gradient(to top, #dd181880, #333333ee); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to top, #dd1818cc, #333333ee); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  background-repeat: no-repeat;
  background-attachment: fixed;
  color: #ffffff;
}
a {
  color: #333366;
  text-decoration: none;
}
a:visited {
  color: black;
}
div {

}
h1 {
  margin: 16px 0;
  font-size: 2.5em;
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
  font-weight: 400;
  line-height: 1.167;
  letter-spacing: 0em;
}

p,
div {
  color: 000;
  font-family: Roboto;
  letter-spacing: 0.15008px;
  font-weight: 300;
  line-height: 24px;
  margin: 0px 0px 0px;
}
